import network from '../network'

// 获取用户信息
function getUserInfo() {
    let user = storage.get("user");
    return network.get("/user/info", {
        params: {
            user_id: user.user_id
        }
    })
}

// 修改和绑定邮箱
function bindEmail(email) {
    let user = storage.get("user");
    let params = {
        email: email,
        user_id: user.user_id
    }
    return network.post("/user/email", params)
}

export default {
    getUserInfo,
    bindEmail
}