<!--  -->
<template>
  <div id="app">
    <div class="top-bg">
      <!-- @/assets/img/1.jpg -->
      <img class="top-bg-image" :src="headImage" />
    </div>
    <div class="flex-cloum main">
      <span class="title margin-center">基本信息</span>

      <div class="user-info flex-row-align-center">
        <img class="head-image" :src="headImage" />
        <span class="nick">{{userInfo.name}}</span>
      </div>

      <el-card class="box-card" :body-style="{ padding: '0px' }">
        <div class="item div-status flex-row-align-center">
          <span class="tips">认证状态</span>
          <span class="tips-content" :style="verifyStyle.text_color">{{verifyStyle.text}}</span>
          <el-button @click="goVerifyPageAction()">{{verifyStyle.btn_text}}</el-button>
        </div>
        <div class="item div-phone flex-row-align-center">
          <span class="tips">联系手机</span>
          <div class="tips-content">{{userInfo.phone}}</div>
          <el-button @click="showDialog('phone')">修改</el-button>
        </div>
        <div class="item div-email flex-row-align-center">
          <span class="tips">联系邮箱</span>
          <span class="tips-content">{{userInfo.email ? userInfo.email : '未绑定'}}</span>
          <el-button @click="showDialog('email')">{{userInfo.email ? "修改" : '绑定邮箱'}}</el-button>
        </div>
      </el-card>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      top="30vh"
      center
      :destroy-on-close="true"
      :close-on-click-modal="false"
    >
      <change-dialog
        :change-type="changeType"
        :phone="userInfo.phone"
        :is-bind-email="true"
        @closeAction="closeDialog()"
      ></change-dialog>
    </el-dialog>
  </div>
</template>

<script>
import ChangeDialog from "@/components/account/ChangeDialog.vue";
import userApi from "@/network/api/user";

import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("user");

export default {
  name: "Account",
  data() {
    return {
      userInfo: {},
      dialogVisible: false,
      changeType: "phone"
    };
  },

  computed: {
    ...mapState(["verifystatus"]),

    headImage() {
      return this.userInfo.headImage
        ? this.userInfo.headImage
        : require("@/assets/img/head.png");
    },
    verifyStyle() {
      if (this.verifystatus == "verified_success") {
        return {
          text: "已认证",
          btn_text: "查看认证",
          text_color: {
            color: "#2ED99D"
          }
        };
      } else if (
        this.verifystatus == "" ||
        this.verifystatus == "verified_no"
      ) {
        return {
          text: "未认证",
          btn_text: "去认证",
          text_color: {
            color: "#FFD07D"
          }
        };
      } else if (this.verifystatus == "verifieding") {
        return {
          text: "认证中",
          btn_text: "去修改",
          text_color: {
            color: "#FFD07D"
          }
        };
      } else if (this.verifystatus == "verified_fail") {
        return {
          text: "认证失败",
          btn_text: "去认证",
          text_color: {
            color: "#FB6C6F"
          }
        };
      }
    }
  },

  //生命周期 - 创建完成（访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    this.requsetData();
  },
  destroyed() {
    console.log("account xxxxxxxxxxx destroyed");
  },
  //方法
  methods: {
    ...mapMutations(["updateVerifyStatus"]),

    showDialog(params) {
      this.changeType = params;
      this.dialogVisible = true;
    },
    closeDialog() {
      this.dialogVisible = false;
      // 刷新数据
      this.requsetData();
    },
    // 去认证
    goVerifyPageAction() {
      if (this.verifyStyle.text === "已认证") {
        // 查看认证
        this.$router.push({ path: "/account/info" });
      } else {
        // 修改认证
        this.$router.push({ path: "/account/Verify" });
      }
      // this.$router.push({ path: "/account/info" });
    },

    // 获取基本信息
    requsetData() {
      userApi
        .getUserInfo()
        .then(res => {
          console.log("用户信息", res);
          this.userInfo = res.data[0];
          // 更新认证状态
          this.updateVerifyStatus(this.userInfo.company_cert);
        })
        .catch(res => {
          console.log("用户信息error", res);
        });
    }
  },
  //引入的组件
  components: {
    ChangeDialog
  }
};
</script>
