import network from '../network'

// 发送验证码
// 新手机号码验证  确认
// let change_phone = req.body.change_phone
function sendCode(params) {
    return network.post("/phone_msg/send_code", params)
}

// 验证验证码
// 新手机号码验证  确认
// let change_phone = req.body.change_phone
function verifyCode(params) {
    return network.post("/phone_msg/verify_code", params)
}

export default {
    sendCode,
    verifyCode
}