<!--  -->
<template>
  <div>
    <!-- 修改手机号内容 -->
    <div class="content-box flex-cloum-align-center" v-if="changeType == 'phone'">
      <span v-if="modifyPhoneStatus != 'finish'" class="title">修改绑定手机号</span>
      <!-- 确认旧手机号 -->
      <div v-if="modifyPhoneStatus == 'smscode'" class="content flex-cloum-align-center">
        <span class="phone-title">当前手机号 +86 {{phone}}</span>
        <div class="input-div flex-row" style="width: 100%;position: relative;">
          <el-input v-model="code" placeholder="请输入短信验证码"></el-input>
          <el-button
            class="code-btn"
            type="primary"
            size="mini"
            round
            @click.stop="getVCode('smscode')"
            :disabled="second!=60"
          >{{vcode_text}}</el-button>
        </div>
        <span class="bottom-tips">
          若原手机号无法接收验证信息，请
          <span style="color: #699BF1;">联系客服</span>
        </span>
      </div>
      <!-- 确认新手机号 -->
      <div v-else-if="modifyPhoneStatus == 'newphone'" class="content flex-cloum-align-center">
        <div
          class="input-div newphone flex-row"
          style="width: 100%;position: relative; margin-top:60px"
        >
          <span class="country-num">+86</span>
          <el-input v-model="change_phone" placeholder="请输入新的手机号"></el-input>
          <el-button
            class="code-btn"
            type="primary"
            size="mini"
            round
            @click.stop="getVCode('newphone')"
            :disabled="second!=60"
          >{{vcode_text}}</el-button>
        </div>
        <el-input style="margin-top: 10px;" v-model="code" placeholder="请输入短信验证码"></el-input>
      </div>
      <!-- 修改成功 -->
      <div v-else-if="modifyPhoneStatus == 'finish'" class="content flex-cloum-align-center">
        <div class="finsh-div flex-cloum-align-center">
          <img src="@/assets/img/shouji@3x.png" />
          <span>手机绑定成功</span>
        </div>
      </div>
      <!-- 提交按钮 -->
      <el-button
        class="submintBtn"
        @click="submintChangePhoneAction()"
      >{{modifyPhoneStatus != 'finish' ? '提交' :'完成'}}</el-button>
    </div>

    <!-- 修改邮箱的内容 -->
    <div class="content-box flex-cloum-align-center" v-else-if="changeType == 'email'">
      <span class="title">{{emalStatus == 'bindmail' ? '绑定邮箱' :'修改绑定邮箱'}}</span>
      <div class="content flex-cloum-align-center" style="margin-top: 80px;">
        <el-input v-model="email" :placeholder="emalStatus == 'bindmail' ? '请输入邮箱地址' :'请输入新邮箱地址'"></el-input>
        <div class="flex-row-align-center">
          <img src="@/assets/img/tishi1@3x.png" style="width:14px;height:14px" />
          <span class="email-tips">请慎重填写，该邮箱将用于接收众筹、关注项目后相关信息</span>
        </div>
      </div>
      <el-button
        class="submintBtn"
        @click="sendEmailVerifyAction()"
      >{{emalStatus == 'bindmail' ? '提交' :'发送验证链接'}}</el-button>
    </div>
  </div>
</template>

<script>
import phoneApi from "network/api/phone";
import userApi from "network/api/user";
let timer;
export default {
  data() {
    return {
      change_phone: "", // 更换的新手机号
      code: "",
      second: 60,
      vcode_text: "获取验证码",
      email: "",
      modifyPhoneStatus: "smscode", // smscode newphone finish  修改手机号码页面样式
      emalStatus: this.isBindEmail ? "modifyemail" : "bindmail" // bindmail modifyemail
    };
  },
  props: {
    phone: "", //原来手机号
    // 修改的类型:  phone email
    changeType: {
      type: String,
      default: "phone"
    },
    // 是否绑定邮箱，没有绑定显示绑定，绑定了显示修改样式
    isBindEmail: {
      type: Boolean,
      default: false
    }
  },
  computed: {},
  //生命周期 - 创建完成（访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {},
  //方法
  methods: {
    // 修改手机号提交
    submintChangePhoneAction() {
      if (this.modifyPhoneStatus == "smscode") {
        this.verifyCodeRequest(null, () => {
          this.resetData();
          this.modifyPhoneStatus = "newphone";
        });
      } else if (this.modifyPhoneStatus == "newphone") {
        this.verifyCodeRequest(this.change_phone, () => {
          this.resetData();
          this.chanageStorageUser(this.change_phone);
          this.modifyPhoneStatus = "finish";
        });
      } else if (this.modifyPhoneStatus == "finish") {
        this.$emit("closeAction");
      }
    },

    resetData() {
      clearInterval(timer);
      this.code = "";
      this.second = 60;
      this.vcode_text = "获取验证码";
    },

    chanageStorageUser(change_phone) {
      let user = storage.get("user");
      user.phone = change_phone;
      storage.set("user", user);
    },

    // 获取验证码
    getVCode(value) {
      let params = {};
      if (value == "smscode") {
        if (!this.phone) {
          this.$message.error("请填写手机号");
          return;
        }
        params = {
          phone: this.phone
        };
      } else if (value == "newphone") {
        if (!this.change_phone) {
          this.$message.error("请填写手机号");
          return;
        }
        params = {
          phone: this.change_phone,
          change: true
        };
      }

      phoneApi
        .sendCode(params)
        .then(res => {
          if (res.data) {
            this.$message({
              message: "短信已发送",
              type: "success"
            });
            this.countDownTimer();
          } else {
            this.$message.error("发送失败");
          }
        })
        .catch(res => {
          this.$message.error("发送失败");
        });
    },

    verifyCodeRequest(change_phone, callback) {
      let params = {
        code: this.code,
        phone: this.phone
      };

      if (change_phone) {
        params.change_phone = change_phone;
        params.user_id = storage.get("user").user_id;
      }
      // 请求验证验证码
      phoneApi
        .verifyCode(params)
        .then(res => {
          this.$message({
            message: "验证成功",
            type: "success"
          });
          callback();
        })
        .catch(res => {
          if (res && res.data?.msg) {
            this.$message.error(res.data.msg);
          }
        });
    },

    // 倒计时
    countDownTimer() {
      timer = setInterval(() => {
        this.second -= 1;
        if (this.second > 0) {
          this.vcode_text = "重新发送(" + this.second + ")";
        } else {
          this.second = 60;
          clearInterval(timer);
          this.vcode_text = "获取验证码";
        }
      }, 1000);
    },

    // 绑定邮箱
    sendEmailVerifyAction() {
      if (!this.email) {
        this.$message.error("请填写邮箱");
      }

      // 修改和绑定邮箱
      userApi
        .bindEmail(this.email)
        .then(res => {
          this.$message({
            message: "链接已发送",
            type: "success"
          });
        })
        .catch(res => {
          if (res && res.data?.msg) {
            this.$message.error(res.data.msg);
          }
        });
    }
  },
  //引入的组件
  components: {}
};
</script>
